import { isBindingCheck } from '@/api/login';
import { payCreate } from '@/api/basic';
import { http } from '@/utils/request';
import mHelper from '@/utils/helper';
import mRouter from '@/utils/router';
import { orderPay } from '@/api/product';
export default {
	// 判断是否公众号（微信H5）
	isWechat() {








	},

	// wxjssdk
	async wxConfigH5(url) {
		if (this.isWechat()) {
			const jsApiList = JSON.stringify([
				'chooseWXPay',
				'scanQRCode',
				'updateAppMessageShareData',
				'updateTimelineShareData'
			]);
			await http
				.post(`${wechatConfig}`, {
					url: url,
					jsApiList, // 需要调用微信的原生方法
					debug: false // 是否打开调试
				})
				.then(r => {
					jweixin.config({
						...r.data
					});
				});
		}
	},

	/*
	 *@des 微信支付
	 *
	 *@param order_group 订单:order;充值:recharge;
	 *@param data 订单 {“order_id”:199} 充值 {“money”:100};
	 */
	async weixinPay(order_group, data, route = '/pages/user/money/success', code) {


































		await http
			.post(`${isBindingCheck}`, {




				oauth_client: 'wechatMp'

			})
			.then(async res => {
				if (res.data.openid) {
					await http
						.post(`${payCreate}`, {
							order_group,
							pay_type: 1,




							trade_type: 'mini_program',

							data,
							openid: res.data.openid
						})
						.then(r => {
















							uni.requestPayment({
								...r.data.config,
								timeStamp: r.data.config.timestamp,
								success: () => {
									mHelper.toast('支付成功');
									mRouter.redirectTo({ route });
								},
								fail: res => {
									mHelper.toast('支付已取消');
								},
								complete: () => {}
							});

						});
				} else {

					uni.login({
						provider: 'weixin',
						success: function(loginRes) {
							http
								.post(`${payCreate}?code=${loginRes.code}`, {
									order_group,
									pay_type: 1,
									trade_type: 'mini_program',
									data
								})
								.then(r => {
									uni.requestPayment({
										...r.data.config,
										timeStamp: r.data.config.timestamp,
										success: () => {
											mHelper.toast('支付成功');
											mRouter.redirectTo({ route });
										},
										fail: res => {
											mHelper.toast('支付已取消');
										},
										complete: () => {}
									});
								});
						},
						fail: function(err) {
							mHelper.log(err);
						}
					});

























				}
			});

	},

	/*
	 *@des 支付宝支付
	 *
	 *@param order_group 订单:order;充值:recharge;
	 *@param data 订单 {“order_id”:199} 充值 {“money”:100};
	 */
	async aliPay(order_group, data, route = '/pages/user/money/success') {





		mHelper.toast('微信小程序不支持支付宝充值');
		return;

		await http
			.post(`${payCreate}`, {
				order_group,
				pay_type: 2,






				data
			})
			.then(r => {
















			});
	},

	/*
	 *@des 余额支付
	 *
	 *@param data 支付参数
	 */
	async balancePay(data, route = '/pages/user/money/success') {
		await http
			.post(`${orderPay}`, {
				order_group: 'order',
				trade_type: 'js',
				pay_type: 5,
				data
			})
			.then(() => {
				mRouter.redirectTo({ route });
			});
	}
};
